import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { Hunit } from '../models/hunit';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { AppService } from 'src/app/app.service';
import { Store, select } from '@ngrx/store';
import { GlobalState } from 'src/app/store/states/global.state';
import { HunitLoadAction, HunitDeleteAction, HunitInsertAction, HunitUpdateAction } from '../store/hunit.actions';
import { selectHunitTotal, selectAllHunit } from 'src/app/pages/account/housingunit/store/hunit.selectors';
import { img } from 'src/app/pages/properties/utils/to-formdata';
import { HunitService } from '../services/hunit.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HunitParams } from '../models/hunit.params';
import { Property } from 'src/app/app.models';
import { PropertyLoad } from 'src/app/pages/properties/models/property.load';
import { PropertyLoadAction } from 'src/app/pages/properties/store/property.actions';

@Component({
  selector: 'app-housingunit',
  templateUrl: './housingunit.component.html',
  styleUrls: ['./housingunit.component.scss']
})
export class HousingunitComponent implements OnInit {
  displayedColumns: string[] = ['id', 'hnumber', 'status', 'balance', 'actions' ];
  dataSource: MatTableDataSource<Hunit>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  public total = 0;
  public hunit: Hunit = {};
  private propertyId: string;
  public property: Property = {};

  constructor(public appService: AppService,
              private store: Store<GlobalState>,
              private hunitService: HunitService,
              private activatedRoute: ActivatedRoute,
              private router: Router,
              public dialog: MatDialog) { }

  ngOnInit() {
    this.store.pipe(select(selectAllHunit)).subscribe(data => {
        this.dataSource = new MatTableDataSource(data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      });


    this.store.pipe(select(selectHunitTotal)).subscribe(total => {
        this.total = total;
       });


    this.activatedRoute.params.subscribe(params => {
      this.propertyId = params.id;
      this.loadHunit({page: 1, limit: 10, property: params.id});

      this.appService.getPropertyById(params.id).subscribe(property => {
        if (property){
          this.property = property;
        }

      });
    });

  }

public loadHunit(data){
   this.store.dispatch(new HunitLoadAction(data));
  }

  public remove(hunit: Hunit) {

    this.store.dispatch(new HunitDeleteAction({hunit}));
    const index: number = this.dataSource.data.indexOf(hunit);
    if (index !== -1) {
      this.dataSource.data.splice(index, 1);
      this.dataSource = new MatTableDataSource<Hunit>(this.dataSource.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  public applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

public img(url: string){
  if (url){
    return img(url);
  }
  return '';

}

public check(property): string{
  if ( property && property.gallery){
    return property.gallery[0].medium;
  }else{
    return null;
  }
}

openEditDialog(hunit: Hunit): void {
  const dialogRef = this.dialog.open(AddHunitComponent, {
    width: '500px',
    data: hunit
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');

    this.hunit = result;
    this.hunit._id = hunit._id;
    this.hunit.propertyId = this.propertyId;
    const hunitParams =  { hunit: this.hunit} as HunitParams;
    this.store.dispatch(new HunitUpdateAction(hunitParams));

  });
}

openDialog(): void {
  const dialogRef = this.dialog.open(AddHunitComponent, {
    width: '500px',
    data: this.hunit
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    this.hunit = result;
    this.hunit.propertyId = this.propertyId;
    const hunitParams =  { hunit: this.hunit} as HunitParams;
    this.store.dispatch(new HunitInsertAction(hunitParams));

  });
}

}

@Component({
  selector: 'app-add-hunit',
  templateUrl: './add-hunit.component.html',
  styleUrls: ['./add-hunit.component.scss']
})
export class AddHunitComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<AddHunitComponent>,
    public formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA)
    public data: Hunit) {

    }
    public title = 'Add Housing units';
    public status = [
      { id: 1, name: 'VACCANT' },
      { id: 2, name: 'TAKEN' },
      { id: 3, name: 'BOOKED' }
    ];
    public type =  { id: 1, name: 'VACCANT' };
    public infoForm: FormGroup;

  public closeDialog(){
    this.dialogRef.close();
  }
  public onInfoFormSubmit(hunit: Hunit){
    if (this.infoForm.valid){
      this.data = hunit;
      this.dialogRef.close();

    }

  }

  ngOnInit(): void {

    this.infoForm = this.formBuilder.group({
      status: ['', Validators.required],
      hnumber: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      floor: [''],
      description: [''],

    });
    if (this.data._id){
      this.title = 'Edit Housing unit ' +   this.data.hnumber;
      this.infoForm.patchValue(this.data);
      this.type =   { id: 1, name: this.data.status };
    }

  }

}

