import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';
import { Store, select } from '@ngrx/store';
import { GlobalState } from 'src/app/store/states/global.state';
import { selectCurrentUser } from '../register/store/user.selectors';
import { User } from '../register/models/user';
import { AuthService } from '../register/services/auth.service';
import { UserLogoutAction } from '../register/store/user.actions';
import { img } from '../properties/utils/to-formdata';

@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.scss']
})
export class AccountComponent implements OnInit {
  constructor(public router: Router, private store: Store<GlobalState>, private authService: AuthService) {

   }
  public psConfig: PerfectScrollbarConfigInterface = {
    wheelPropagation:  true
  };
  @ViewChild('sidenav') sidenav: any;
  public sidenavOpen = true;
  public links = [

    { name: 'My Properties', href: 'property-groups', icon: 'apartment' },
    { name: 'Manage Properties', href: 'featured', icon: 'settings' },
    { name: 'Customer Feedback', href: 'feedback', icon: 'list_alt' },
    { name: 'Blog posts', href: 'blog', icon: 'how_to_reg' },
    { name: 'Mailing list', href: 'mailing-list', icon: 'attach_email' },
    { name: 'Bookmarks', href: 'favorites', icon: 'bookmarks' },
    { name: 'Account', href: 'profile', icon: 'person' },
  { name: 'Logout', href: '/login', icon: 'power_settings_new' },
  ];
  public user: User;


  ngOnInit() {
    if (window.innerWidth < 960){
      this.sidenavOpen = false;
    }
    this.store.pipe(select(selectCurrentUser)).subscribe(user => this.checkUser(user) );
  }
  private checkUser(user: User){

    this.user = user;
  }

  @HostListener('window:resize')
  public onWindowResize(): void {
    (window.innerWidth < 960) ? this.sidenavOpen = false : this.sidenavOpen = true;
  }


  public openLink($event: MouseEvent, link) {
    if (link.name === 'Logout') {
        $event.stopPropagation();
        this.authService.logout();
        this.store.dispatch(new UserLogoutAction());
        this.router.navigate(['/login']);

    }
    return;

    // navigates to /test
}

public img(url: string){
  if (url){
    return img(url);
  }
  return 'assets/images/others/user.jpg';

}

ngAfterViewInit() {
    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        if (window.innerWidth < 960){
          this.sidenav.close();
        }
      }
    });
  }


}
