import { Component, OnInit } from '@angular/core';
import { GlobalState } from 'src/app/store/states/global.state';
import { Store, select } from '@ngrx/store';
import { HunitLoadAction } from '../../store/hunit.actions';
import { ActivatedRoute } from '@angular/router';
import { selectAllHunit } from '../../store/hunit.selectors';
import { map } from 'rxjs/operators';
import { Hunit } from '../../models/hunit';

@Component({
  selector: 'app-single-unit',
  templateUrl: './single-unit.component.html',
  styleUrls: ['./single-unit.component.scss']
})
export class SingleUnitComponent implements OnInit {

  constructor( private store: Store<GlobalState>, private activatedRoute: ActivatedRoute) { }
  public hunit: Hunit = {};
  ngOnInit(): void {


  this.activatedRoute.params.subscribe(params => {
    this.store.pipe(select(selectAllHunit)).pipe(map(hunits => hunits.find(x => x._id === params.id))).subscribe(unit => {
    if  (unit){
        this.hunit = unit;
    }
    });
    this.store.dispatch(new HunitLoadAction({_id: params.id}));
  });

  }

}
