import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { Property } from 'src/app/app.models';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { Pgroup } from 'src/app/shared/pgroup/models/pgroup';
import { AppService } from 'src/app/app.service';
import { Store, select } from '@ngrx/store';
import { GlobalState } from 'src/app/store/states/global.state';
import { ActivatedRoute, Router } from '@angular/router';
import { selectPropertyTotal } from '../../properties/store/property.selectors';
import { selectAllPgroup } from 'src/app/shared/pgroup/store/pgroup.selectors';
import { map } from 'rxjs/operators';
import { PgroupLoadAction } from 'src/app/shared/pgroup/store/pgroup.actions';
import { PropertyLoadAction, PropertyDeleteAction } from '../../properties/store/property.actions';
import { img } from '../../properties/utils/to-formdata';
import { MediaObserver, MediaChange } from '@angular/flex-layout';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-featured',
  templateUrl: './featured.component.html',
  styleUrls: ['./featured.component.scss']
})
export class FeaturedComponent implements OnInit {
  displayedColumns: string[] = ['id', 'image', 'title', 'published', 'views', 'actions' ];
  dataSource: MatTableDataSource<Property>;
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  public total = 0;
  public pgroupId = '';
public viewType = 'grid';
  public viewCol = 33.3;
  public count = 8;
  public watcher: Subscription;
  public properties: Property[] = [];
  public pgroup: Pgroup =  {title: 'Properties'};

  constructor(public appService: AppService,
              private store: Store<GlobalState>,
              private activatedRoute: ActivatedRoute,
              private router: Router,
              private mediaObserver: MediaObserver,
     ) {

     this.watcher =   mediaObserver.media$.subscribe((change: MediaChange) => {
        // console.log(change)
        if (change.mqAlias == 'xs') {
          this.viewCol = 100;
        }
        else if (change.mqAlias == 'sm'){
          this.viewCol = 50;
        }
        else if (change.mqAlias == 'md'){
          this.viewCol = 33.3;
        }
        else{
          this.viewCol = 33.3;
         // this.viewCol = 25;
        }
      });
      }

  ngOnInit() {
      this.appService.getProperties().subscribe(data => {
        this.dataSource = new MatTableDataSource(data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.properties = data;
      });

      this.store.pipe(select(selectPropertyTotal)).subscribe(total => {
        this.total = total;
       });

      this.activatedRoute.params.subscribe(params => {
        this.loadProperties({page: 1, limit: 10, pgroup: params.id});
        this.pgroupId = params.id;
        this.store.pipe(select(selectAllPgroup))
        .pipe(map(properties => properties.find(x => x._id === this.pgroupId)))
         .subscribe(item => { if (item){this.pgroup = item; }});
        this.store.dispatch(new PgroupLoadAction({_id: this.pgroupId}));
       });
      this.store.dispatch(new PropertyLoadAction({page: 1, limit: 10}));

  }

  public loadProperties(data){
    this.store.dispatch(new PropertyLoadAction(data));
  }


  public remove(property: Property) {
    this.store.dispatch(new PropertyDeleteAction(property));
    const index: number = this.dataSource.data.indexOf(property);
    if (index !== -1) {
      this.dataSource.data.splice(index, 1);
      this.dataSource = new MatTableDataSource<Property>(this.dataSource.data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  public applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

public img(url: string){
   return img(url);
}

ngOnDestroy() {
  this.watcher.unsubscribe();
}
}
