import { createSelector, createFeatureSelector } from '@ngrx/store';
import { hunitAdapter, HunitState } from './hunit.state';


export const {
  selectIds: _selectHunitDataIds,
  selectEntities: _selectHunitEntities,
  selectAll: _selectAllHunit,
  selectTotal: _selectHunitTotal,
} = hunitAdapter.getSelectors();

export const selectHunitState = createFeatureSelector<HunitState>('hunit');

export const selectHunitIds = createSelector(
  selectHunitState,
  _selectHunitDataIds,
);

export const selectHunitEntities = createSelector(
  selectHunitState,
  _selectHunitEntities,
);

export const selectAllHunit = createSelector(
  selectHunitState,
  _selectAllHunit,
);

export const selectHunitError = createSelector(
  selectHunitState,
  (state: HunitState): boolean => state.error,
);

export const selectHunitLoading = createSelector(
  selectHunitState,
  (state: HunitState): boolean => state.loading,
);

export const selectHunitTotal = createSelector(
  selectHunitState,
  (state: HunitState): number => state.total,
);
