import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { SharedModule } from '../../shared/shared.module';
import { InputFileModule } from 'ngx-input-file';
import { AgmCoreModule } from '@agm/core';
import { AccountComponent } from './account.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { MyPropertiesComponent } from './my-properties/my-properties.component';
import { FavoritesComponent } from './favorites/favorites.component';
import { ProfileComponent } from './profile/profile.component';
import { EditPropertyComponent } from './edit-property/edit-property.component';
import { AuthGuard } from '../register/services/auth.guard';
import { SecurityComponent } from './security/security.component';
import { SubmitPropertyComponent } from '../submit-property/submit-property.component';
import { HunitService} from './housingunit/services/hunit.service';
import { HousingunitComponent, AddHunitComponent } from './housingunit/housingunit/housingunit.component';
import { SingleUnitComponent } from './housingunit/housingunit/single-unit/single-unit.component';
import { PgroupComponent } from 'src/app/shared/pgroup/ui/pgroup.component';
import { FeaturedComponent } from './featured/featured.component';
import { SubmitPropertyModule } from '../submit-property/submit-property.module';
import { FeedbackComponent } from 'src/app/shared/feedback/ui/feedback.component';
import { PublicationComponent } from 'src/app/shared/publication/ui/publication.component';
import { EmailComponent } from 'src/app/shared/email/ui/email.component';
import { OwnerComponent } from 'src/app/shared/owner/ui/owner.component';


export const routes = [
  {
    path: '',
    component: AccountComponent, children: [
      { path: '', redirectTo: 'profile', pathMatch: 'full' },
      { path: 'property-groups', component: PgroupComponent},
      { path: 'property-groups/:id', component: MyPropertiesComponent},
      { path: 'my-properties', component: MyPropertiesComponent },
      { path: 'my-properties/:id', component: EditPropertyComponent },
      { path: 'favorites', component: FavoritesComponent },
      { path: 'blog', component: PublicationComponent },
      { path: 'feedback', component: FeedbackComponent },
      { path: 'profile', component: ProfileComponent },
      { path: 'submit-property', component: SubmitPropertyComponent },
      { path: 'submit-property/:id', component: SubmitPropertyComponent },
      { path: 'hunits/:id', component: HousingunitComponent },
      { path: 'singleunit/:id', component: SingleUnitComponent },
      { path: 'owner/:id', component: OwnerComponent },
      {path: 'security', component: SecurityComponent},
      {path: 'mailing-list', component: EmailComponent},
      {path: 'featured', component: FeaturedComponent},
    ], canActivate: [AuthGuard],
  },
];

@NgModule({
  declarations: [
    DashboardComponent,
    AccountComponent,
    MyPropertiesComponent,
    FavoritesComponent,
    ProfileComponent,
    EditPropertyComponent,
    SecurityComponent,
    HousingunitComponent,
    AddHunitComponent,
    SingleUnitComponent,
    FeaturedComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    SharedModule,
    InputFileModule,
    AgmCoreModule,
    SubmitPropertyModule,
  ],
  providers: [HunitService],
})
export class AccountModule { }
