import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { emailValidator, matchingPasswords } from 'src/app/theme/utils/app-validators';
import { MatSnackBar } from '@angular/material/snack-bar';
import { User } from '../../register/models/user';
import { Store, select } from '@ngrx/store';
import { GlobalState } from 'src/app/store/states/global.state';
import { selectCurrentUser } from '../../register/store/user.selectors';
import { UserUpdateAction, UserLogoutAction } from '../../register/store/user.actions';
import { UserParams } from '../../register/models/user-params';
import { AuthService } from '../../register/services/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
  public infoForm: FormGroup;
  public user: User;
  constructor(public formBuilder: FormBuilder,
              public snackBar: MatSnackBar,
              public authService: AuthService,
              private router: Router,
              private store: Store<GlobalState>) { }
  public userTypes = [
    { id: 1, name: 'Agent' },
    { id: 2, name: 'Agency' },
    { id: 3, name: 'Buyer' },
    { id: 7, name: 'Seller' },
    { id: 4, name: 'LandLord' },
    { id: 5, name: 'CareTaker' },
    { id: 6, name: 'Tenant' },
  ];
  ngOnInit() {
    this.infoForm = this.formBuilder.group({
      userType: ['', Validators.required],
      fullName: ['', Validators.compose([Validators.required, Validators.minLength(3)])],
      phone: ['', Validators.required],
      idNumber: [''],
      image: null,
      receiveNewsletter: false,
      organization: null,

    });

    this.store.pipe(select(selectCurrentUser)).subscribe(user => this.checkUser(user) );
  }
  private checkUser(user: User){
    this.user = user;
    this.infoForm.patchValue(user);
  }

  public logOut(){
    this.authService.logout();
    this.store.dispatch(new UserLogoutAction());
    this.router.navigate(['/login']);
  }

  public onInfoFormSubmit(values: Object): void {
    if (this.infoForm.valid) {
      console.log(values);
      const user = values as User;
      user.userType = (values as any).userType.name;
      user._id = this.user._id;

      let file: File = null;
      if ((values as any).image) {
      file = ((values as any).image[0].file) as File;
      }
      const userParams =  { user, file} as UserParams;
      this.store.dispatch(new UserUpdateAction(userParams));
      this.snackBar
      .open('Your account information updated successfully!',
      'cancel', { panelClass: 'success', verticalPosition: 'bottom', duration: 3000 });
    }
  }


}
